body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.maincontainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
 }
 
 
 .maincard {
   width: 700px;
   height: 500px;
   background: url(/Complex-Credit-Card-React/static/media/GC16-Omni-4_1.7cd5e06e.jpg) no-repeat;
   background-size: cover;
   border: 1px;
   border-radius: 20px;
   padding: 20px;
   margin-top: 50px;
   margin-left: 50px;
 }
 
 .maincontainer h1 {
   color: white;
   font-weight: bold;
   text-align: end;
   font-size: 40px;
 }
 
 .information {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: space-around;
           justify-content: space-around;
 }
 
 .informationuser {
   color: white;
   display: -webkit-flex;
   display: flex;
   -webkit-flex-direction: column;
           flex-direction: column;
   width: 70%;
   height: 200px;
 }
 
 .logo1 {
   width: 130px;
   height: 100px;
 
 }
 
 .logo2 {
   width: 100px;
   height: 150px;
   -webkit-align-self: end;
           align-self: end;
 
 }
 
 .valid {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: space-between;
           justify-content: space-between;
   -webkit-align-items: center;
           align-items: center;
   width: 180px;
 }
 
 .month h5 {
 
   font-weight: bold;
   font-size: 30px;
   margin-top: 0px;
 
 }
 
 .month p {
   margin-bottom: 0px;
 }
 
 .check {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: space-between;
           justify-content: space-between;
   -webkit-align-items: center;
           align-items: center;
   width: 100%
 }
 
 .number1 {
   font-size: 40px;
   margin-bottom: 0;
   background-color: #DFCA28;
   font-family: 'Courier New', Courier, monospace;
   font-weight: bold;
   text-align: center;
 }
 
 .number2 {
   font-size: 30px;
   margin-bottom: 0;
   margin-top: 0;
   font-weight: bold;
 }
 
 .holder {
   font-size: 30px;
   font-weight: bold;
   margin-top: 0;
 }
 .unserinput {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
 }
 .unserinput p {
  font-size: 20px;
  font-weight: bold;
  }
 
 input{
  width: 300px;
   height: 40px;
   border: 2px inset rgb(95, 165, 189);
   font-size: 20px;
   text-align: center;
 }
